import React from "react";

function SocialMedia() {
    return (
        <>
            <div className="container">
                <div className="social-media__container--header row">
                    <div
                        className="social-media__image-box"
                        id="insta-img"
                    ></div>
                    <div
                        className="social-media__info-box"
                        id="insta-info"
                    ></div>
                </div>
                <div className="social-media__container row" id="insta"></div>
            </div>
            <script>{getInstaApiJson()}</script>
        </>
    );
}

const getInstaApiJson = () => {
    fetch("https://www.instagram.com/summonersewcase/?__a=1")
        .then(function (response) {
            if (response.status !== 200) {
                console.log(
                    "Looks like there was a problem. Status Code: " +
                        response.status
                );
                return;
            }
            
            // Examine the content in the response
            response.json().then(function (data) {
                var numberOfShownPosts = 3;
                var instaArray = [];

                for (var i = 0; i < numberOfShownPosts; i++) {
                    let mediaItem = {};

                    const timelineMedia =
                        data.graphql.user.edge_owner_to_timeline_media.edges[i]
                            .node;

                    mediaItem.imageUrl =
                        timelineMedia.thumbnail_resources[2].src;

                    mediaItem.redirectUrl = timelineMedia.shortcode;

                    instaArray.push(mediaItem);
                }

                var profileImage = data.graphql.user.profile_pic_url;

                var infoText = data.graphql.user.biography;
                showInstaPosts(instaArray, profileImage, infoText);
            });
        })
        .catch(function (err) {
            console.log("Fetch Error :-S", err);
        });
};

const showInstaPosts = (instaArray, profileImage, infoText) => {
    if (instaArray != null && profileImage != null && infoText != null) {
        instaArray.forEach((mediaItem) => {
            var container = document.createElement("div");
            container.setAttribute(
                "class",
                "col-12 col-md-6 col-lg-4 social-media__container"
            );
            
            var atag = document.createElement("a");
            atag.setAttribute(
                "href",
                "https://www.instagram.com/p/" + mediaItem.redirectUrl
            );
            atag.setAttribute("target", "_blank");

            var img = document.createElement("img");
            img.src = mediaItem.imageUrl;
            img.alt = "";

            atag.append(img);
            container.append(atag);

            var parentContainer = document.getElementById("insta");

            if (parentContainer != null) {
                parentContainer.appendChild(container);
            }
        });

        var profileImg = document.createElement("img");
        profileImg.src = profileImage;
        profileImg.alt = "Profile Image";

        var title = document.createElement("div");
        title.setAttribute("class", "social-media__title");
        title.innerHTML = "SummonerSewcase";

        var desc = document.createElement("div");
        desc.innerHTML = infoText;

        var getImageContainer = document.getElementById("insta-img");
        var getInfoContainer = document.getElementById("insta-info");

        if (getImageContainer != null && getInfoContainer != null) {
            getImageContainer.appendChild(profileImg);
            getInfoContainer.appendChild(title);
            getInfoContainer.appendChild(desc);
        }
    }
};

export default SocialMedia;
