import React from "react";

import HeroImage from "../components/HeroImage";
import TextContainer from "../components/TextContainer";
import { ConventionListContainerUpcoming } from "../components/ConventionListContainerUpcoming";
import { ConventionListContainerPrevious } from "../components/ConventionListContainerPrevious";

import { ConventionLandingPageContent } from "../content/Convention/ConventionLandingPageContent";
import { ConventionAboutContent } from "../content/Convention/ConventionAboutContent";
import { conventionListContent } from "../content/Convention/ConventionListContent";

function Conventions() {
    return (
        <>
            <section className="hero-image hero-image--conventions">
                {HeroImage(ConventionLandingPageContent)}
            </section>

            <section className="conventions">
                <div className="container">
                    {TextContainer(ConventionAboutContent)}
                </div>
            </section>

            <section className="conventions">
                {ConventionListContainerUpcoming(conventionListContent)}
            </section>

            <section className="conventions">
                {ConventionListContainerPrevious(conventionListContent)}
            </section>
        </>
    );
}

export default Conventions;
