import React from "react";

function TextContainer(content) {
    var textObject = content.text;
    var text = textObject.map((textElement, key) => (
        <p key={key}>{textElement.text}</p>
    ));

    return (
        <>
            <div className="text-container__headline">
                <h2>{content.headline}</h2>
            </div>
            <div className="text-container__text">{text}</div>
        </>
    );
}

export default TextContainer;
