import React from "react";

export function FAQContainer(content) {
    return (
        <div className="container">
            {content.map((element, index) => {
                return (
                    <div className="faq__container" key={index}>
                        <div className="faq__headline">{element.headline}</div>
                        <div className="faq__content">{element.text}</div>
                    </div>
                );
            })}
        </div>
    );
}
