import React from "react";

export const ConventionListContainerUpcoming = (content) => {
    return (
        <div className="container">
            <h2>Upcoming Events</h2>
            <div className="row">
                {content.map((element, index) => {
                    var now = new Date();
                    var elementDate = new Date(element.date);

                    if (now > elementDate) {
                        return "";
                    }

                    return (
                        <div
                            className="col-12 col-md-4 conventions__list--container"
                            key={index}
                        >
                            <a href={element.link}>
                                <div
                                    className={
                                        "conventions__image conventions__image--" +
                                        element.image
                                    }
                                ></div>
                            </a>
                            <div className="conventions__list">
                                <div>
                                    <div className="conventions__list--headline">
                                        {element.title}
                                    </div>
                                    <div className="conventions__list--content">
                                        {element.desc}
                                    </div>
                                </div>
                                <a className="conventions__list--cta" href="/">
                                    {element.ctaText}
                                </a>
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};
