import React from "react";

export const FAQSpecialThanksContainer = (content) => {
    return (
        <div className="container">
            {content.map((element, index) => {
                return (
                    <div className="faq__container" key={index}>
                        <a
                            href={element.href}
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <div className="faq__headline faq__headline--special">
                                <div>{element.headline}</div>
                            </div>
                            <div className="faq__content">{element.text}</div>
                        </a>
                    </div>
                );
            })}
        </div>
    );
};
