import React from "react";

import Parallax from "../components/Parallax";
import About from "../components/About";
import Info from "../components/Info";
import HeroImage from "../components/HeroImage";
import SocialMedia from "../components/SocialMedia";

import { Infocontent } from "../content/About/AboutInfoContent";
import { LandingPageContent } from "../content/About/AboutLandingPageContent";
import { AboutContent } from "../content/About/AboutContent";
import { imageContent } from "../content/About/AboutImageContent";

function AboutUs() {
    return (
        <>
            <section className="hero-image hero-image--about">
                {HeroImage(LandingPageContent)}
            </section>
            <section className="info">{Info(Infocontent)}</section>
            <section className="about">
                {About(AboutContent, imageContent)}
            </section>
            <section className="parallax">
                {Parallax("parallax--primary", "350px")}
            </section>
            <section className="social-media">{SocialMedia()}</section>
        </>
    );
}

export default AboutUs;
