import React from "react";

import HeroImage from "../components/HeroImage";
import TextContainer from "../components/TextContainer";
import { FAQContainer } from "../components/FAQContainer";
import { FAQSpecialThanksContainer } from "../components/FAQSpecialThanksContainer";

import { FAQAboutContent } from "../content/FAQ/FAQAboutContent";
import { FAQLandingPageContent } from "../content/FAQ/FAQLandingPageContent";
import { FAQListContent } from "../content/FAQ/FAQListContent";
import { FAQListContentSpecial } from "../content/FAQ/FAQListContentSpecial";

function Faq() {
    return (
        <>
            <section className="hero-image hero-image--faq">
                {HeroImage(FAQLandingPageContent)}
            </section>

            <section className="faq">
                <div className="container">
                    {TextContainer(FAQAboutContent)}
                </div>
            </section>

            <section className="faq faq--list">{FAQContainer(FAQListContent)}</section>

            <section className="faq faq--list">{FAQSpecialThanksContainer(FAQListContentSpecial)}</section>
        </>
    );
}

export default Faq;
