import React from "react";
import { Link } from "react-router-dom";
import { useWindowDimensions } from "../utility/windowDImensions";

const linkArray = [
    { path: "/", linkText: "About Us" },
    { path: "/conventions", linkText: "Conventions" },
    { path: "/organizers", linkText: "Organizers" },
    { path: "/faq", linkText: "FAQ" },
];

const navigate = () => {
    toggleBurgerMenu();

    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
}

const toggleBurgerMenu = () => {
    var navIcon = document.getElementById("nav-icon");
    var navBar = document.getElementById("nav-bar");

    if (navIcon != null && navBar != null){
        navIcon.classList.toggle("burger-menu--open");
        navBar.classList.toggle("header-mobile__navigation--open");
    
        preventScrolling();
    }
};

const preventScrolling = () => {
    document.getElementsByTagName("BODY")[0].classList.toggle("header-burger-prevent-scroll");
}

const desktopHeader = () => {
    return (
        <div className="desktop-header">
            <a href="/">
                <div className="header-icon"></div>
            </a>
            <div className="header__container">
                {linkArray.map((element, index) => {
                    return (
                        <div key={index} className="header__item">
                            <Link
                                to={element.path}
                                onClick={navigate}
                                className="header__item--link"
                            >
                                {element.linkText}
                            </Link>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

const mobileHeader = () => {
    return (
        <nav className="mobile-header">
            <div id="nav-icon" onClick={toggleBurgerMenu}>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div id="nav-bar" className="header-mobile__navigation">
                <div className="header-mobile__list">
                    {linkArray.map((element, index) => {
                        return (
                            <div
                                key={index}
                                className="header-mobile__list--item"
                            >
                                <Link
                                    onClick={navigate}
                                    to={element.path}
                                    className="header-mobile__link"
                                >
                                    {element.linkText}
                                </Link>
                            </div>
                        );
                    })}
                </div>
            </div>
        </nav>
    );
};

function Header() {
    const { width } = useWindowDimensions();
    var header;

    if (width > 768) {
        header = desktopHeader();
    } else {
        header = mobileHeader();
    }
    return <>{header}</>;
}

export default Header;
