import React from "react";

function ImageContainer(imageContent, index) {
    return (
        <div className=" col-12 col-md-6 col-lg-4" key={index}>
            <a href="/" className="about__image">
                <img
                    src={require("../dist/images/" +
                        imageContent.image +
                        ".jpg")}
                    alt=""
                />
                <div className="about__image--text">{imageContent.text}</div>
            </a>
        </div>
    );
}

export default ImageContainer;
