import React from "react";

import HeroImage from "../components/HeroImage";
import Info from "../components/Info";

import { LandingPageContent } from "../content/Organizers/OrganizersLandingPageContent";
import { Infocontent } from "../content/Organizers/OrganizersInfoContent";

function Organizers() {
    return (
        <>
            <section className="hero-image hero-image--organizers">
                {HeroImage(LandingPageContent)}
            </section>
            <section className="info">{Info(Infocontent)}</section>
            <section>
                <div className="container">COMING SOON</div>
            </section>
        </>
    );
}

export default Organizers;
