import React from "react";

function HeroImage(content) {
    return (
        <div className="hero-image__block">
            <div className="hero-image__content">
                <div className="hero-image__sub-headline">
                    {content.headline}
                </div>
                <div className="hero-image__headline">
                    {content.subHeadline}
                </div>
            </div>
        </div>
    );
}

export default HeroImage;
